import React from 'react'
import { Container, Row } from 'react-bootstrap'

const Index = () => {
  return (
    <Container fluid>
      <Row>
        <h1>Admin Role Change</h1>
      </Row>
      <div className="row">
        <div className="col-md-10">
          <div className="card">
            <div className="card-header">
              <h3 className="card-title">Roles</h3>
            </div>

            <div className="card-body table-responsive p-0">
              <table className="table table-head-fixed text-nowrap">
                <thead>
                  <tr>
                    <th>SN</th>
                    <th>Role</th>
                    <th>Short</th>
                    <th>Amount</th>
                    <th>Super Admin Charge</th>
                    <th>Admin Amount</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>1</td>
                    <td>Admin</td>
                    <td>AD</td>
                    <td>2999</td>
                    <td>1000</td>
                    <td>20000</td>
                  </tr>
                  <tr>
                    <td>2</td>
                    <td>Master Distributor</td>
                    <td>MDT</td>
                    <td>300</td>
                    <td>1</td>
                    <td>200</td>
                    
                  </tr>
                  <tr>
                    <td>3</td>
                    <td>Retailer</td>
                    <td>RT</td>
                    <td>51</td>
                    <td>0</td>
                    <td>11</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </Container>
  )
}

export default Index
